<template>
  <div>
    <div style="width:100%;height:60px;float:left;line-height:60px;">
      <span style="margin-left:60px;">点餐</span>
    </div>
    <div
      style="width:20%;height:1000px;float:left;line-height:40px;text-align:center;"
    >
      <a href="#/"
        ><p style="width:100%;height:40px;line-height:40px;background:green;">
          水饺
        </p></a
      >
      <a href="#/a2"
        ><p style="width:100%;height:40px;line-height:40px;background:green;">
          流食
        </p></a
      >
      <a href="#/a4"
        ><p style="width:100%;height:40px;line-height:40px;background:green;">
          凉菜
        </p></a
      >
      <a href="#/a5"
        ><p style="width:100%;height:40px;line-height:40px;background:green;">
          包子
        </p></a
      >
      <a href="#/a6"
        ><p style="width:100%;height:40px;line-height:40px;background:green;">
          庆丰福粽
        </p></a
      >
      <a href="#/a7"
        ><p style="width:100%;height:40px;line-height:40px;background:green;">
          庆丰特色冷饮
        </p></a
      >
      <a href="#/a8"
        ><p style="width:100%;height:40px;line-height:40px;background:green;">
          豆浆饮品
        </p></a
      >
    </div>
    <div style="width:79%;height:140px;float:left;background:pink;">
      <div>
        <div style="width:50%;height:50%;float:left;">
          <img
            style="width:100%;height:100%;"
            src="https://f10.baidu.com/it/u=3835442366,3635772668&fm=72"
            alt=""
          />
        </div>
        <div>
          <p>猪肉大葱</p>
          <p>包子</p>
          <span>￥8</span>
          <div class="coutter-wrapper">
            <button type="button" @click="plus">+</button>
            <button type="button">{{ result }}</button>
            <button type="button" @click="minus">-</button>
          </div>
        </div>
      </div>
    </div>
    <div style="width:79%;height:140px;float:left;background:pink;">
      <div>
        <div style="width:50%;height:50%;float:left;">
          <img
            style="width:100%;height:100%;"
            src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1758271658,1084910114&fm=26&gp=0.jpg"
            alt=""
          />
        </div>
        <div>
          <p>香菇白菜包子</p>
          <p>包子</p>
          <span>￥8</span>
          <div class="coutter-wrapper">
            <button type="button" @click="plus">+</button>
            <button type="button">{{ result }}</button>
            <button type="button" @click="minus">-</button>
          </div>
        </div>
      </div>
    </div>
    <div style="width:79%;height:240px;float:left;background:pink;">
      <div>
        <div style="width:50%;height:50%;float:left;">
          <img
            style="width:100%;height:100%;"
            src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=4030968880,1551131695&fm=26&gp=0.jpg"
            alt=""
          />
        </div>
        <div>
          <p>猪肉大葱</p>
          <p>包子</p>
          <span>￥8</span>
          <div class="coutter-wrapper">
            <button type="button" @click="plus">+</button>
            <button type="button">{{ result }}</button>
            <button type="button" @click="minus">-</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      result: 0
    };
  },
  methods: {
    minus() {
      this.result--;
      this.$emit("input", { res: this.result, other: "--" });
    },
    plus() {
      this.result++;
      this.$emit("input", { res: this.result, other: "++" });
    }
  }
};
</script>

<style>
a {
  text-decoration: none;
  color: #fff;
}
</style>



