import Vue from 'vue'
import Router from 'vue-router'
import n1 from './n1.vue'
import n2 from './n2.vue'
import a1 from './a1.vue'
import a2 from './a2.vue'
import a3 from './a3.vue'
import a4 from './a4.vue'
import a5 from './a5.vue'
import a6 from './a6.vue'
import a7 from './a7.vue'
import a8 from './a8.vue'


Vue.use(Router)

var routes = [
		    {
		    	path:'/',
		    	name:'n1',
		    	component:n1
		    },
		     {
		    	path:'/n2',
		    	name:'n2',
		    	component:n2
			},
			{
		    	path:'/',
		    	name:'a1',
		    	component:a1
		    },
		     {
		    	path:'/a2',
		    	name:'a2',
		    	component:a2
			},
			{
		    	path:'/a3',
		    	name:'a3',
		    	component:a3
		    },
		     {
		    	path:'/a4',
		    	name:'a4',
		    	component:a4
			},
			{
		    	path:'/a5',
		    	name:'a5',
		    	component:a5
		    },
		     {
		    	path:'/a6',
		    	name:'a6',
		    	component:a6
			},
			{
		    	path:'/a7',
		    	name:'a7',
		    	component:a7
		    },
		     {
		    	path:'/a8',
		    	name:'a8',
		    	component:a8
		    }
]

export default new Router({
	routes
})



